<script>
export default {
  methods: {
    getType(item) {
      if (item.crew_count > 50) {
        return "primary";
      } else {
        return "danger";
      }
    }
  },
  data() {
    return {
      buttons: [
        { name: "", crew_count: 55 },
        { name: "", crew_count: 30 },
        { name: "", crew_count: 180 },
        { name: "", crew_count: 8 },
        { name: "", crew_count: 70 }
      ]
    };
  }
};
</script>

