<template>
  <div>
    <el-tag>{{d1}}</el-tag>
    <el-tag type="success">+</el-tag>
    <el-tag>{{d2}}</el-tag>
=
    <el-input v-model="val"></el-input>
    <p v-if="d1+d2==val && max_second>0 "></p>
    <p v-if="max_second<=0"></p>
    <p v-if="d1+d2!=val && max_second>0">{{max_second}}</p>
  </div>
</template>
<script>
export default {
  watch: {
    val(v) {
      if (v == this.d1 + this.d2) {
        this.pop();
      }
    },
  },
  beforeDestroy() {
    clearInterval(window.it);
  },
  data() {
    return {
      d1: 1,
      d2: 2,
      val: 0,
      max_second: 10,
      show: true,
      n: 0,
    };
  },
  created() {
    let that = this;
    window.it = setInterval(function () {
      that.max_second--;
    }, 1000);
    this.d1 = this.random();
    this.d2 = this.random();
  },
  methods: {
    random() {
      return parseInt(Math.random() * 10);
    },
    pop() {
      this.n++;
      this.$confirm(`${this.n}?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.d1 = this.random();
          this.d2 = this.random();
          this.val = "";
          this.max_second = 10;
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
};
</script>
<style  scoped>
.el-input {
  width: 150px;
}
</style>


